
.article {
  composes: fullScreen from "../../commenStyle/layout.css";
  background-color: #fff;
}
.author {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100px;
  border-bottom: 2px solid #e5e5e5;
}
.avator {
  width: 60px;
  height: 60px;
  margin: 0 20px;
  background-color: #ccc;
  border-radius: 50%;
}
.name {
  font-size: 32px;
}
.publishTime {
  margin-top: 6px;
  color: var(--次要);
}
.authorAction {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 0 20px;
  height: 100%;
}
.focus {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: var(--主题色);
  width: 100px;
  height: 60px;
  font-size: 28px;
  border-radius: 6px;
  color: #fff;
}
.main {
  overflow: auto;
  padding: 0 20px 80px;
}
.title {
  padding: 20px 0;
  font-size: 48px;
  line-height: 78px;
  font-weight: bold;
  text-align: justify;
}
.about {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 20px;
}
.anthology, .read {
  margin: 0 10px;
  color: var(--次要);
  font-size: 22px;
}
.mainBody {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.action {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 80px;
  background-color: #ccc;
  z-index: 1600;
}



