  height: 56px;
  width: 56px;
  margin: 12px 0 10px 10px;
  font-size: 30px;
  padding: 9px 12px 10px 7px;
}
.editor .v-note-wrapper .v-note-op .left .op-icon-divider, .editor .v-note-wrapper .v-note-op .right .op-icon-divider {
